export const addCollaborator = ({ emailId, collaboratorRole }) => {
    const userData = store.getState().moderation.details;

    if (userData && Object.keys(userData).length > 0) {
        return new HttpClient().post(`${Add_Collaborator}/${localStorage.getItem('wallId')}`, { wallName: userData.wall.name, emailId: emailId, collaboratorRole: collaboratorRole })
    }
    else {
        return null
    }
}



export const updateCollaborator = ({ collaboratorId, id, collaboratorRole }) => {
    const { details } = store.getState().moderation;
    if (details && Object.keys(details).length > 0) {
        return new HttpClient().post(`${Update_Collaborator}/${id}/${localStorage.getItem('wallId')}`, { wallName: details.wall.name, collaboratorRole: collaboratorRole, collaboratorId })
    }
    else {
        return null
    }

}

