export const GET_INSTAGRAM_IMAGE = (instagramUrl) => axios.get(instagramUrl)

const FETCH_LINK = (fetchUrl, dataSrc) => GET_INSTAGRAM_IMAGE(fetchUrl).then((response) => {
    let html = response.data;
    if (html) {
        var regex = /_sharedData = ({.*);<\/script>/m,
            json = JSON.parse(regex.exec(html)[1]);
        if (json) {
            var post_id_instagram = json.entry_data.PostPage[0].graphql.shortcode_media.id;
            var edges = json.entry_data.PostPage[0].graphql.shortcode_media.display_url;
            var video = '';

            let time_stamp_date = json.entry_data.PostPage[0].graphql.shortcode_media.taken_at_timestamp;
            let content_text = (json.entry_data.PostPage[0].graphql.shortcode_media.edge_media_to_caption.edges[0]) ? json.entry_data.PostPage[0].graphql.shortcode_media.edge_media_to_caption.edges[0].node.text : '';
            var type = (content_text) ? 4 : 2;
            let post_file = (json.entry_data.PostPage[0].graphql.shortcode_media.thumbnail_src) ? json.entry_data.PostPage[0].graphql.shortcode_media.thumbnail_src : edges;
            let insta_owner_id = json.entry_data.PostPage[0].graphql.shortcode_media.owner.id;
            let profile_pic_thumbnail = json.entry_data.PostPage[0].graphql.shortcode_media.owner.profile_pic_url;
            let full_name = json.entry_data.PostPage[0].graphql.shortcode_media.owner.full_name;
            let username = json.entry_data.PostPage[0].graphql.shortcode_media.owner.username;

            if (json.entry_data.PostPage[0].graphql.shortcode_media.is_video) {
                video = json.entry_data.PostPage[0].graphql.shortcode_media.video_url;
                type = (content_text) ? 5 : 3;
            }

            return [{ 'video': video, 'type': type, 'post_id_instagram': post_id_instagram, 'time_stamp_date': time_stamp_date, 'content_text': content_text, 'post_file': post_file, 'insta_owner_id': insta_owner_id, 'profile_pic_thumbnail': profile_pic_thumbnail, 'full_name': full_name, 'username': username }]
        }
    }
}).catch((error) => {
    if (dataSrc.includes("cdn.taggbox.com")) return ImagePlaceholder;
    else return ImagePlaceholder;
})


export const POPUP_IMAGE_RENEW_REQUEST = async (e) => {
    let postLinkId = e.split('/');
    let fetchUrl = `https://images${~~(Math.random() * 3333)}-focus-opensocial.googleusercontent.com/gadgets/proxy?container=none&url=${e}`;

    await FETCH_LINK(fetchUrl, postLinkId)
        .then((response) => { return response })
        .catch((error) => { return false })
}








